* {
  margin: 0px;
  padding: 0px;
}
.Navbar {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  font-size: 20px;
  font-weight: 700;
  padding-left: 40px;
  background-color: white;
  top: 20px;
  margin-top: -20px;
  padding-top: 20px;
  z-index: 10;
}

.Navbar > div {
  padding-bottom: 15px;
}

.SrDiv {
  font-weight: 700;
  font-size: 20px;
  color: #ff6263;
}

.LinksDiv {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 100px;
  display: flex;
  gap: 20px;
  justify-content: right;
  padding-right: 40px;
  align-items: center;
}

.activeStyle {
  font-weight: 700;
  font-size: 20px;
  text-decoration: underline teal;
  color: #ff6263;
}

.inactiveStyle {
  font-size: 20px;
  font-weight: 600;
  color: black;
}

.HamburgerDiv {
  display: none;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 8px;
}

@media screen and (max-width: 450px) and (min-width: 70px) {
  .Navbar {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 15px;
    padding-top: 20px;
  }

  .LinksDiv {
    display: none;
  }

  .HamburgerDiv {
    display: block;
    color: black;
    font-weight: 600;
  }

  .Navbar > .heading {
    height: 100%;
    padding-top: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (max-width: 750px) and (min-width: 450px) {
  .Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 15px;
    padding-top: 20px;
  }
  .LinksDiv {
    display: none;
  }

  .HamburgerDiv {
    display: block;
    font-weight: 700;
  }
}

@media screen and (max-width: 900px) and (min-width: 750px) {
  .Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
  }
  .LinksDiv {
    display: none;
  }

  .HamburgerDiv {
    display: block;
    font-weight: 700;
    font-size: 20px;
  }
}
